






























    import Vue from 'vue';

    export default Vue.extend({
        data() {
            return {
                tel: '',
                callFinished: false,
                callError: false
            };
        },

        created() {
            this.tel = this.$route.query.tel;
        },

        methods: {
            doCall() {
                this.callInProgress = true;

                return window.plugins.CallNumber.callNumber(() => {
                    this.callFinished = true;
                }, result => {
                    console.log('ERROR', result);

                    this.callError = true;
                    this.callFinished = true;
                }, this.tel);
            },

	        goBack(){
                this.$router.go(-1);
	        }
        }
    });
